.amsterdam-paragraph {
  font-size: clamp(
    1.125rem,
    calc(
      1.125rem + 0.00375 * (100vw - 20rem)
    ),
    1.5rem
  );
  line-height: clamp(
    1.8125rem,
    calc(
      1.8125rem + 0.005625 * (100vw - 20rem)
    ),
    2.375rem
  );
  margin-block: 0;
}

.amsterdam-paragraph-intro {
  font-size: clamp(
    1.375rem,
    calc(
      1.375rem + 0.005 * (100vw - 20rem)
    ),
    1.875rem
  );
  line-height: clamp(
    2.1875rem,
    calc(
      2.1875rem + 0.00625 * (100vw - 20rem)
    ),
    2.8125rem
  );
}

.amsterdam-paragraph-small {
  font-size: clamp(
    1rem,
    calc(
      1rem + 0.00125 * (100vw - 20rem)
    ),
    1.125rem
  );
  line-height: clamp(
    1.5rem,
    calc(
      1.5rem + 0.003125 * (100vw - 20rem)
    ),
    1.8125rem
  );
}

