@import "../../node_modules/normalize.css/normalize.css";
body {
  font-family: "Amsterdam Sans", "Arial", sans-serif;
  font-size: clamp(
    1.125rem,
    calc(
      1.125rem + 0.00375 * (100vw - 20rem)
    ),
    1.5rem
  );
  font-weight: 400;
  line-height: 160%;
}

/* Use border-box sizing by default for all elements */
*,
*::before,
*::after {
  box-sizing: border-box;
}

