@font-face {
  font-display: swap;
  font-family: "Amsterdam Sans";
  font-style: normal;
  font-weight: 300;
  src: url("./eot/AmsterdamSans-Light.eot?#iefix");
  src: url("./eot/AmsterdamSans-Light.eot?#iefix") format("eot"),
    url("./woff2/AmsterdamSans-Light.woff2") format("woff2"), url("./woff/AmsterdamSans-Light.woff") format("woff");
}
