.amsterdam-heading {
  break-after: avoid;
  break-inside: avoid;
  color: black;
  font-weight: 800;
  margin-block-end: 0;
  margin-block-start: 0;
  margin-inline-end: 0;
  margin-inline-start: 0;
}

.amsterdam-heading-1 {
  font-size: clamp(
    2rem,
    calc(
      2rem + 0.03 * (100vw - 20rem)
    ),
    5rem
  );
  line-height: clamp(
    2.1875rem,
    calc(
      2.1875rem + 0.028125 * (100vw - 20rem)
    ),
    5rem
  );
}

.amsterdam-heading-2 {
  font-size: clamp(
    1.75rem,
    calc(
      1.75rem + 0.0175 * (100vw - 20rem)
    ),
    3.5rem
  );
  line-height: clamp(
    2.125rem,
    calc(
      2.125rem + 0.0175 * (100vw - 20rem)
    ),
    3.875rem
  );
}

.amsterdam-heading-3 {
  font-size: clamp(
    1.5rem,
    calc(
      1.5rem + 0.01 * (100vw - 20rem)
    ),
    2.5rem
  );
  line-height: clamp(
    2rem,
    calc(
      2rem + 0.01 * (100vw - 20rem)
    ),
    3rem
  );
}

.amsterdam-heading-4 {
  font-size: clamp(
    1.375rem,
    calc(
      1.375rem + 0.00625 * (100vw - 20rem)
    ),
    2rem
  );
  line-height: clamp(
    1.9375rem,
    calc(
      1.9375rem + 0.004375 * (100vw - 20rem)
    ),
    2.375rem
  );
}

.amsterdam-heading-5 {
  font-size: clamp(
    1.125rem,
    calc(
      1.125rem + 0.00375 * (100vw - 20rem)
    ),
    1.5rem
  );
  line-height: clamp(
    1.5625rem,
    calc(
      1.5625rem + 0.00375 * (100vw - 20rem)
    ),
    1.9375rem
  );
}

.amsterdam-heading-6 {
  font-size: clamp(
    1rem,
    calc(
      1rem + 0.00125 * (100vw - 20rem)
    ),
    1.125rem
  );
  line-height: clamp(
    1.375rem,
    calc(
      1.375rem + 0.000625 * (100vw - 20rem)
    ),
    1.4375rem
  );
}

