.amsterdam-icon {
  align-items: center;
  display: inline-flex;
  height: clamp(
    1.5rem,
    calc(
      1.5rem + 0.00125 * (100vw - 20rem)
    ),
    1.625rem
  );
  margin-inline-end: 12px;
  /* Offset icon by 2px, because Amsterdam Sans vertical alignment is off */
  padding-block-end: 2px;
}

.amsterdam-icon svg {
  fill: currentColor;
}

.amsterdam-icon-small svg {
  height: 1rem;
  width: 1rem;
}

.amsterdam-icon-medium svg {
  height: 1.5rem;
  width: 1.5rem;
}

.amsterdam-icon-large svg {
  height: 2rem;
  width: 2rem;
}

